<template>
  <div>
      <van-nav-bar title="精选"></van-nav-bar>
        <!-- size是每个商品的高度 remain是一次显示多少行商品 -->
        <virtual-list :size="vlistSize" :remain="10" class="vlist" style="height:calc(100vh - 96px)"
          :onscroll="handleScroll" ref="vlist">
          <router-link :to="`/product/${item.id}`" class="item" v-for="(item, index) in list" :key="`${Math.random()*100}-${item.id}`">
            <img v-lazy="item.cover">
            <h3>{{item.name}}</h3>
            <div class="price clearfix">
              <span class="price-l">￥</span>
              <span class="cprice">{{item.price}}</span>
              <span class="price-r">起</span>
            </div>
          </router-link>
          <van-loading v-if="isLoading" />
        </virtual-list>

  </div>
</template>

<script>
  import virtualList from 'vue-virtual-scroll-list'
  export default {
    components: { 'virtual-list': virtualList },
    data() {
      return {
        page: 1,
        total: 1,
        isLoading: false,
        list: []
      }
    },
    computed: {
      vlistSize() {
        return screen.availWidth * 1.3
      }
    },
    methods: {
      handleScroll(e, params) {
        if (this.page == this.total) {
          return
        }
        if (this.isLoading) {
          return
        }
        if (params.offset >= params.offsetAll - 400) {
          this.isLoading = true
          this.page++
          this.getData()
        }
      },
      getData() {
        let params = {
          page:this.page,
          size:10
        }
        this.$axios.get(`/product/pagination`, {
          params
        }).then(res => {
          if (res.errcode == 0) {
            let data = res.data
            this.total = data.totalPages
            this.list = this.list.concat(data.data)
            this.$store.skeletonVisible = false
          }
          this.isLoading = false
        })
      }
    },
    mounted() {
      this.getData()
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .van-loading {
    text-align: center;
    padding-bottom: 20px;
  }

  .my-tab {
    height: 10vw;
    width: 100vw;
    display: flex;
    align-items: center;
    overflow-y: hidden;
    flex-wrap: nowrap;
  }

  .my-tab a{
    display: inline-block;
    padding: 0 20vw;
    text-align: center;
    line-height: 10vw;
    white-space: nowrap;
  }

  .van-swipe {
    margin-bottom: 10px;
  }

  .van-swipe-item img {
    width: 100%;
    height: 40vw;
  }

  .van-icon-search {
    font-size: 5vw;
  }

  .vlist {
    height: calc(100vh - 50px - 46px);
  }

  .vlist .item {
    float: left;
    width: 100vw;
    height: 130vw;
  }

  .vlist .item img {
    width: 100vw;
    height: 100vw;
  }

  .vlist .item h3 {
    padding: 1.5vw 0 0 3vw;
    color: #333;
    font-size: 4.2vw;
    height: 7vw;
    overflow: hidden;
  }


  .vlist .item .price {
    padding: 1vw 0 0 3vw;
    font-size: 3.6vw;
  }

  .vlist .item .price span {
    float: left;
  }

  .vlist .item .cprice {
    color: #EA625B;
    font-size: 3.6vw;
  }

  .vlist .item .price-l {
    color: #EA625B;
    font-size: 3vw;
    margin-top: 0.2vw;
  }

  .vlist .item .price-r {
    color: #EA625B;
    font-size: 0.24vw;
    margin-left: 0.4vw;
    margin-top: 1vw;
  }
</style>


